.table {
    min-width: 754px;
}

.table .item-name {
    width: 128px;
}

.table .item-image {
    width: 100px;
}

.table .item-price {
    width: 96px;
}

.table .item-option {
    width: 96px;
}

@charset "UTF-8";

.box.alert-box {
    position: fixed;
    top: 40vh;
    left: 40vw;
    width: 200px;
    z-index: 777;
}

@charset "UTF-8";

.upload-control {
    width: 200px;
    height: 200px;
    border: 1px solid #dbdbdb;
    border-radius: 4px;
}

.upload-icon {
    position: absolute;
    top: 20px;
    right: 20px;
    bottom: 20px;
    left: 20px;
    width: 160px !important;
    height: 160px !important;
    z-index: 666;
}

.upload-image {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    border-radius: 4px;
    z-index: 777;
}

.upload-input {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    cursor: pointer;
    z-index: 888;
}

.upload-invisible {
    opacity: 0;
}
